// feedback-section
.feedback-container {
  padding: 40px 16px;
  background: var(--feedback-background-color);
}
.feedback-container h2 {
  color: #000;
  font-family: 'Merriweather';
  font-size: 16px;
  line-height: 1.4;
  margin-bottom: 24px;
}
.feedback-container img {
  border-radius: 4px;
  width: 100%;
}

// slider
.slider-slide {
  width: 250px;
  border-radius: 4px;
  border: 1px solid var(--border-grey);
  padding: 10px;
  background-image: linear-gradient(
      to bottom,
      rgba(255, 255, 255, 0.5),
      rgba(255, 255, 255, 1)
    ),
    url('../images/mob-menu-w320.webp');
  background-color: lightgray;
  background-position: 50%;
  height: 260px;
  margin-right: 12px; /* Отступ справа для создания промежутка */
  margin-left: 0px;
}
.slider-slide h3 {
  color: #000;
  font-family: 'Merriweather';
  font-size: 11px;
  line-height: 1.4;
}

.stars{
  display:none;
}

.stars-mob {
  display: flex;
  justify-content: flex-start;
  gap: 4px;
  margin-top: 4px;
  margin-bottom: 12px;
}
.stars-mob > li > svg {
  width: 10px;
  height: 10px;
}
.quotes {
  display: flex;
  margin-bottom: 4px;
}
.quotes svg {
  width: 5px;
  height: 7px;
}
.slider-text {
  color: #000;
  font-family: 'Roboto';
  font-size: 11px;
  font-style: normal;
  font-weight: 400;
  line-height: 1.4; /* 15.4px */
}
.quotes-bottom {
  justify-content: flex-end;
}

.feedback-slider {
  margin-top: 12px;
}

.slider-wrapper {
  display: flex;
  transition: transform 0.5s ease;
}
.slick-slide {
  margin-left: 30px; /* Смещение вправо на 20px */
}

